.code {
  /* Base16 Tomorrow Night */
  --base00: #1d1f21;
  --base01: #282a2e;
  --base02: #373b41;
  --base03: #969896;
  --base04: #b4b7b4;
  --base05: #c5c8c6;
  --base06: #e0e0e0;
  --base07: #ffffff;
  --base08: #cc6666;
  --base09: #de935f;
  --base0A: #f0c674;
  --base0B: #b5bd68;
  --base0C: #8abeb7;
  --base0D: #81a2be;
  --base0E: #b294bb;
  --base0F: #a3685a;
}

:root {
  /* Base16 Default Dark */
  --base00: #181818;
  --base01: #282828;
  --base02: #383838;
  --base03: #585858;
  --base04: #b8b8b8;
  --base05: #d8d8d8;
  --base06: #e8e8e8;
  --base07: #f8f8f8;
  --base08: #ab4642;
  --base09: #dc9656;
  --base0A: #f7ca88;
  --base0B: #a1b56c;
  --base0C: #86c1b9;
  --base0D: #7cafc2;
  --base0E: #ba8baf;
  --base0F: #a16946;
}

body {
  background: var(--base00);
  color: var(--base05);
  font-family: sans-serif;
  max-width: 70ch;
  padding: 2ch;
  margin: auto;
  line-height: 1.5;
  text-align: justify;
}

body.light {
  background: #f8f8f8;
  color: #383838;
}

article {
  padding-bottom: 1rem;
}

a {
  color: var(--base0D);
}

a:not(:hover) {
  text-decoration: none;
}

ul {
  list-style-type: none;
  padding: 0;
}
